import { api, ApiResponse, PaginationResponse } from './config';

export interface Fornecedor {
  id: number;
  nome: string;
  email: string;
  telefone: string;
  cnpj: string;
  endereco: {
    cep: string;
    rua: string;
    numero: string;
    complemento?: string;
    bairro: string;
    cidade: string;
    estado: string;
  };
}

export interface CreateFornecedorDTO {
  nome: string;
  email: string;
  telefone: string;
  cnpj: string;
  endereco: {
    cep: string;
    rua: string;
    numero: string;
    complemento?: string;
    bairro: string;
    cidade: string;
    estado: string;
  };
}

export const fornecedorService = {
  async create(data: CreateFornecedorDTO): Promise<ApiResponse<Fornecedor>> {
    const response = await api.post<ApiResponse<Fornecedor>>('/fornecedores', data);
    return response.data;
  },

  async list({
    page = 1,
    perPage = 10,
    search = ''
  } = {}): Promise<PaginationResponse<Fornecedor[]>> {
    const response = await api.get<PaginationResponse<Fornecedor[]>>('/fornecedores', {
      params: { page, perPage, search }
    });
    return response.data;
  },

  async getById(id: number): Promise<ApiResponse<Fornecedor>> {
    const response = await api.get<ApiResponse<Fornecedor>>(`/fornecedores/${id}`);
    return response.data;
  },

  async update(id: number, data: Partial<CreateFornecedorDTO>): Promise<ApiResponse<Fornecedor>> {
    const response = await api.put<ApiResponse<Fornecedor>>(`/fornecedores/${id}`, data);
    return response.data;
  },

  async delete(id: number): Promise<ApiResponse<void>> {
    const response = await api.delete<ApiResponse<void>>(`/fornecedores/${id}`);
    return response.data;
  },

  async getTotal(): Promise<ApiResponse<number>> {
    const response = await api.get<ApiResponse<number>>('/fornecedores/count/total');
    return response.data;
  }
};
